

<script>
  import { Doughnut } from 'vue-chartjs'

  export default {
    extends: Doughnut,
    name: "DoughnutChart",
	props:["data"],
	mounted(){
	let chartData = {
      labels:[],
      datasets: [{
        borderWidth: 1,
		borderColor: [
		'rgba(255,99,132,1)',
        'rgba(54, 162, 235, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(228, 82, 27, 1)'		
        ],
		backgroundColor:[
		'rgba(255,99,132,1)',
        'rgba(54, 162, 235, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(228, 82, 27, 1)'		
        ],
		borderAlign: 'inner',
		data: []
       }]
	};
	
	for (let category in this.data) {
      chartData.labels.push(category);
      chartData.datasets[0].data.push(this.data[category]);
    }

    this.renderChart(chartData, {
      legend: {
        display: true,
		position: 'right'
      },
      responsive: true,
      maintainAspectRatio: false
    });
  }
};
</script>