
<script>
import { HorizontalBar  } from "vue-chartjs";

export default {
  extends: HorizontalBar ,
  name: "BarChart",
  props: ["data"],
  mounted() {
    // reformat in the way the lib wants
    let chartData = {
      labels: [],
      datasets: [
        {
          borderWidth: 1,
          borderColor: [
              'rgba(255, 159, 64, 0.8)',
              'rgba(75, 192, 192, 0.8)',
              'rgba(255,99,132,0.8)',
              'rgba(255, 206, 86, 0.8)',
              'rgba(54, 162, 235, 0.8)',
              'rgba(228, 82, 27, 0.8)',
              'rgba(153, 102, 255, 0.8)',],
          backgroundColor:[
              'rgba(255, 159, 64, 0.8)',
              'rgba(75, 192, 192, 0.8)',
              'rgba(255,99,132,0.8)',
              'rgba(255, 206, 86, 0.8)',
              'rgba(54, 162, 235, 0.8)',
              'rgba(228, 82, 27, 0.8)',
              'rgba(153, 102, 255, 0.8)',],
          data: []
        }]
    };

    for (let category in this.data) {
      chartData.labels.push(category);
      chartData.datasets[0].data.push(this.data[category]);
    }

    this.renderChart(chartData, {
      legend:{
        display: false
      },
      responsive: true,
      maintainAspectRatio: false,
    });
  }
};
</script>
