<template>
  <v-container class="text-center">
    <v-container v-if="show == false">
    <p>This may take a few minutes</p>
    <v-progress-circular :size="70" :width="7" color="amber" indeterminate></v-progress-circular>
    </v-container>
    <!-- <v-lazy v-else :options="{ threshold: .5 }" > -->
	<div v-else>
	<v-row>
		<v-col md='6' sm='12'>
		<h2>Total mention</h2>
			<BarChart1 v-if="loaded" :data="totalMentions" :styles="styles"/>
		</v-col>
		<v-col md='6' sm='12'>
		<h2>The trend of total mention per Day</h2>
			<LineChart v-if="loaded" :data="pageViews" :styles="styles"/>
		</v-col>
	</v-row>
	<v-row>
		<v-col md='6' sm='12'>
		<h2>The trend of total mention per Week</h2>
			<LineChart2 v-if="loaded" :data="pageViewW" :styles="styles"/>
		</v-col>
		<v-col md='6' sm='12'>
		<h2>The trend of total mention per Month</h2>
			<LineChart3 v-if="loaded" :data="pageViewM" :styles="styles"/>
		</v-col>
	</v-row>
	<v-row>
		<Explain :text="totalME.text" :themeColor="totalME.theme" id="totalME"/>
	</v-row>
    </div>
    <!-- </v-lazy> -->
  </v-container>
</template>


<script>
import BarChart1 from '@/components/BarChart1'
import LineChart from '@/components/LineChart'
import LineChart2 from '@/components/LineChart2'
import LineChart3 from '@/components/LineChart3'
import Explain from '@/components/Landing/Explain'


export default {
  name: "App",
  components: {
    BarChart1,
    LineChart,
	LineChart2,
	LineChart3,
	Explain
  },
  
  data() {
    return {
      loaded: false,
      totalMentions: null,
      pageViews: null,
      totalM: null,
      pageViewM: null,
      pageViewW: null,
      visitors: null,
      styles: {
        width: "750px",
        height: "450px",
        position: "center"
      },
      totalME: {
      text: 'Total 42959 mentions represented in the graphics above.  We can see the trend drastically increasing from  March, 12. On March, 14 Indonesian Minister of Transportation, Budi Karya COVID-19 positive confirmed, as we can see the highest peak is on March, 14. After that, the trend is still high in March but keeps falling each day, and the lowest is on April,17. As we can see in the monthly graphic, the trend is relatively low in April.',
      theme: 'no-color'
    }
    };
  },
  async created() {
    let resp = await fetch(
      "https://api.jsonbin.io/b/5ef6cb362406353b2e0d80b9/13"
    );
    let data = await resp.json();
    console.log("DATA", data);
	this.totalMentions = data.totalM;
    this.pageViews = data.pageViews;
	this.pageViewM = data.pageViewM;
	this.pageViewW = data.pageViewW;
    this.loaded = true;
  }
};
</script>


