
<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  name: "LineChart4",
  props: ["data"],
  mounted() {
    // reformat in the way the lib wants
    let chartData = {
      labels: [],
      datasets: [
        {
          label: "Joy",
          borderColor: 'rgba(255, 159, 64, 1)',
          data: [],
          fill: false
        },
		{
          label:"Trust",
          borderColor: 'rgba(75, 192, 192, 1)',
          data: [],
          fill: false
		},
		{
          label:"Fear",
          borderColor: 'rgba(255,99,132,1)',
          data: [],
          fill: false
		},
		{
          label:"Surprise",
          borderColor: 'rgba(255, 206, 86, 1)',
          data: [],
          fill: false
		},
		{
          label:"Sadness",
          borderColor: 'rgba(54, 162, 235, 1)',
          data: [],
          fill: false
		},
		{
          label:"Anger",
          borderColor: 'rgba(228, 82, 27, 1)',
          data: [],
          fill: false
		},
		{
          label:"Anticipation",
          borderColor: 'rgba(153, 102, 255, 1)',
          data: [],
          fill: false
		}
      ]
    };

    for (let i = 0; i < this.data.length; i++) {
      chartData.labels.push(this.data[i].date);
      chartData.datasets[0].data.push(this.data[i].joy);
      chartData.datasets[1].data.push(this.data[i].trust);
      chartData.datasets[2].data.push(this.data[i].fear);
      chartData.datasets[3].data.push(this.data[i].surprise);
      chartData.datasets[4].data.push(this.data[i].sadness);
      chartData.datasets[5].data.push(this.data[i].anger);
      chartData.datasets[6].data.push(this.data[i].anticipation);
    }

    this.renderChart(chartData, {
      responsive: true,
      maintainAspectRatio: false
    });
  }
};
</script>
