
<script>
import { Bar  } from "vue-chartjs";

export default {
  extends: Bar ,
  name: "BarChart",
  props: ["data"],
  mounted() {
    // reformat in the way the lib wants
    let chartData = {
      labels: [],
      datasets: [
        {
          borderWidth: 1,
          borderColor: [
              'rgba(75, 192, 192, 0.8)',
              'rgba(75, 192, 192, 0.8)',],
          backgroundColor:[
              'rgba(75, 192, 192, 0.8)',
              'rgba(75, 192, 192, 0.8)',],
          data: []
        }]
    };

    for (let category in this.data) {
      chartData.labels.push(category);
      chartData.datasets[0].data.push(this.data[category]);
    }

    this.renderChart(chartData, {
      legend:{
        display: false
      },
      responsive: true,
      maintainAspectRatio: false,
    });
  }
};
</script>
